
/*in edit ,it is the background of the edit inputs*/
form {
  margin: 20px;
  display: flex;
  flex-direction: column;

}

input {
  margin: 10px 0;
}

button {
  padding: 10px;
  background-color: #3498db;
  color: white;
  border: none;
  cursor: pointer;
}
/*bckground of shop */
.product-list {
  display: flex;

  
}
/*background of shop */
.product-card {

  margin: 10px;
  padding: 10px;

 
}

.product-card img {
  width: 100%;
  height: 150px;
  object-fit:fill;
 
}
.category{
  background-color: chocolate;
}
