
@media (min-width: 320px) {

    .nav {
        background-color: black;
        width: 100%;
        margin-top: -2vh;
        height:10vh;
        padding: 1vw;
        margin-left: 0vw;
    }
    
    .navbarlink {
        color: white;
    }
    
    .nav-links {
        display: flex;
        flex-direction: row;
        color: white;
        list-style: none;
        gap: 3vw;
        align-items: center;
    }
    
    .home, .about, .contact-navbar, .footer-navbar, .track-navbar, 
    .navbar-shophomepage, .navbar-cart, .navbar-checkout, .logged, 
    .navloginbutton, .edit-shop-button, .logout-button, .addpost-button, 
    .logout-button, .loginlink,.logged-on {
        color: white;
        text-decoration: none;
        font-size: 1.5vw;
    }
    
    .logout-button {
        margin-left: 45vw;
    }
    
    .logged-on {
        margin-left: 40vw;
        width: 10vw;
        position: absolute;
        color: red;
        margin-top: -2vh;
    }
    
    .edit-shop-button {
        margin-left: 60vw;
        position: absolute;
        color: gray;
    }
    
    .edit-link-nav {
        color: grey;
    }
    
    .addpost-button {
        color: gray;
    }
    
    .addpostnav {
        color: grey;
        margin-left: 15vw;
        width: 8vw;
        margin-top: -2vh;
        position: absolute;
    }
    .toggle-tracker-button{
        background-color: chartreuse;
        }
        .toggle-gshop-button{
            background-color: darkgreen;
        
        }
        
}

