.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
}

.modal-content {
    background: white;
    padding: 20px;
    border-radius: 5px;
    position: relative;
    max-width: 500px;
    margin: auto;
}

.close-button {
    position: absolute;
    top: 10px;
    right: 15px;
    cursor: pointer;
}
