.home {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  color: #333;
  font-family: 'Arial', sans-serif;
}
.comment {
  border: 1px solid #ccc;
  padding: 10px;
  margin: 10px 0;
  background-color: #f9f9f9;
}

.reply {
  border: 1px solid #ddd;
  padding: 8px;
  margin: 5px 0 5px 20px; /* Indent replies */
  background-color: #eef;
}

.admin-controls {
  margin-bottom: 20px;
}

.posts-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.post {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.post:hover {
  transform: scale(1.02);
}

.post-title {
  font-size: 1.5em;
  margin: 0 0 10px;
}

.post-content {
  font-size: 1em;
  margin: 0 0 15px;
}

.post-image {
  width: 100%;
  height: auto;
  border-radius: 4px;
  margin-bottom: 15px;
}

.read-more-button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
}

.read-more-button:hover {
  background-color: #0056b3;
}

.delete-button,
.edit-button {
  margin-top: 10px;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
}

.delete-button {
  background-color: #dc3545;
  color: white;
}

.edit-button {
  background-color: #28a745;
  color: white;
}

.categories-container {
  margin-top: 30px;
}

.categories-container h2 {
  margin-bottom: 10px;
}

.categories-container ul {
  list-style: none;
  padding: 0;
}

.categories-container li {
  cursor: pointer;
  padding: 5px 0;
}

.edit-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba
}
.solid{
  width: 45vw;
}
.editpostform-container{
background-color: #28a745;

}
@keyframes borderAnimation {
  0% {
      border-color: #3498db; /* Change to your preferred starting color */
  }
  50% {
      border-color: #e74c3c; /* Change to your preferred mid color */
  }
  100% {
      border-color: #3498db; /* Return to the starting color */
  }
}

.animated-border {
  border-width: 4px; /* Adjust as needed */
  border-style: solid;
  border-image: linear-gradient(90deg, #3498db, #e74c3c, #3498db);
  border-image-slice: 1;
  animation: borderAnimation 3s infinite; /* Animation duration and infinite loop */
  transition: border-width 0.3s ease;
}

.animated-border:hover {
  border-width: 6px; /* Increase border width on hover */
}

.yellowdots-background {
  background-image: url('../images/yellowdots.jpg'); /* Ensure path is correct */
  background-repeat: repeat;
}

.stars-background {
  background-image: url('../images/stars.jpg'); /* Ensure path is correct */
  background-repeat: no-repeat;
  background-size: cover; /* or any desired size */
}

.post-check{
  border-width:2px ;
  border-color:black ;
  border-style: solid;
}
