.footer {
    position: absolute;
    background-color: black;
    width: 98%;
    height: 40%;
    margin-left: 0.3vw;

}

.footer a {
    color: white; /* Default link color */
    text-decoration: none; /* Remove underline */
    list-style: none;
}
.font{
    color: aqua;
    position: absolute;
}
.footer-links-home a,
.footer-links-about a,
.footer-links-shophomepage a,
.footer-links-tracking a,
.footer-links-contact a,
.footer-links-terms a {
    color: white; /* Ensure links are white */
    list-style: none;
}

.footer-links-contact a,
.footer-links-about a {
    
}
.category1-footer{
    text-align: center;
    margin-left: 0vw;
    font-size: 1.5vw;
    margin-top: 5vh;
    position: absolute;
  
    list-style: none;
}
.category2-footer {
    text-align: center;
    margin-left: 80vw;
    font-size: 1.5vw;
    margin-top: 5vh;
    position: absolute;
  
    list-style: none;
  
}
.privacyterms{
    font-size:1.5vw ;
    margin-left: -12vw;
    width: 30vw;
    position:absolute ;
}
.footer-links-tracking,.footer-links-contact,.footer-links-terms{
    margin-top: 3vh;
    list-style: none;
    font-size: 1.5vw;
    margin-left: -20vw;
}
.footer-links-about,.footer-links-home,.footer-links-shophomepage{
    margin-top: 3vh;
    list-style: none;
    font-size: 1.5vw;
    
}
.title-input-form {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    list-style: none;
    position: absolute;
  
}


.font-size-selector {
    margin-left: 25vw;
    padding: 5px;
    width: 10vw;
    margin-top: -10vh;
    position: absolute;
}

.submit-button-footer {
  
    background-color: white;
    color: black;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    width: 5vw;
    margin-left: 20vw;
    margin-top: 25vh;
    position: absolute;
}

.submit-button:hover {
    background-color: lightgray;
}
.submitted-titles{
    margin-top: 45vh;
   
    list-style: none;
    margin-left: 35vw;
    position: absolute;
    height: 5vh;

  
}
.ul-footer{
    z-index: 300;
    margin-top: -30vh;
    list-style: none;
    position: absolute;
}
.delete-button-footer{
    margin-left: 20vw;
    list-style: none;
    position: absolute;
    margin-top: 0vh;
    background-color: coral;
    width:3vw;
    height: 6vh;
}

.footer-components-container{
   
    position: absolute;
    margin-top: 0vh;
    height: 8vh;
    width: 100%;
}
.collapsible-button{
    background-color: aqua;
}




.input-one-fontsize{

    position: absolute;
    height: 30vh;
    margin-top: -3vh;
    margin-left: -20vw;

}
.input-one-fontstyle{
   
    position: absolute;
    height: 30vh;
    margin-top: -3vh;
    margin-left: -12vw;
}
.input-one-font-color{

    position: absolute;
    height: 30vh;
    margin-top: -3vh;
    margin-left: 0vw;
}
.title-input-one {
    padding: 10px;
    margin-right: 10px;
    border: none;
    border-radius: 4px;
  
    width: 10vw;
    margin-top: -2vh;
    margin-left: 30vw;
    position: absolute;
}

.title-input-two {
    padding: 10px;
    margin-right: 10px;
    border: none;
    border-radius: 4px;
   
    width: 10vw;
    margin-top: 3vh;
    margin-left: 20vw;
    position: absolute;
}

.input-two-size{

    position: absolute;
    height: 30vh;
    margin-top: 3vh;
    margin-left: -20vw;

}
.input-two-fontstyle{
   
    position: absolute;
    height: 30vh;
    margin-top: 3vh;
    margin-left: -12vw;
  
}
.input-two-fontcolor{

    position: absolute;
    height: 30vh;
    margin-top: 3vh;
    margin-left: 0vw;
}
.previewone{
    margin-left: 40vw;
    margin-top: 10vh;
    width: 30vw;
    z-index: 200;
    position: absolute;
  
}
.previewtwo{
    margin-left: 40vw;
    margin-top: 13vh;
    width: 30vw;

    position: absolute;
 
}
.submitted-titles{
    margin-left: 37vw;
    margin-top: 40vh;
    width: 30vw;

    position: absolute;
}

