.checkout-form {
    max-width: 500px;
    margin: auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  
  .checkout-title {
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .checkout-items-container {
    margin-bottom: 20px;
  }
  
  .checkout-item {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 10px;
    background-color: #fff;
  }
  
  .checkout-img {
    width: 80px;
    height: auto;
    margin-right: 15px;
    border-radius: 5px;
  }
  
  .checkout-item-info {
    flex: 1;
  }
  
  .checkout-item-title {
    font-size: 18px;
    margin: 0;
  }
  
  .checkout-item-price {
    color: #888;
  }
  
  .checkout-delete {
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .checkout-total {
    text-align: right;
    font-size: 20px;
    margin-top: 10px;
  }
  
  .checkout-user-info {
    margin: 20px 0;
  }
  
  .checkout-user-info label {
    display: block;
    margin-bottom: 10px;
  }
  
  .checkout-user-info input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .checkout-card-element {
    margin: 20px 0;
  }
  
  .checkout-submit-button {
    width: 100%;
    padding: 10px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .checkout-submit-button:disabled {
    background-color: #ccc;
  }
