/* SideDrawer.css */
.side-drawer {
    position: fixed;
    top: 0;
    left: -250px; /* Hide off-screen */
    width: 250px;
    height: 100%;
    background: #fff;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
    transition: left 0.3s ease-out;
    z-index: 1000;
}

.side-drawer.open {
    left: 0; /* Show on-screen */
}

.side-drawer nav ul {
    list-style: none;
    padding: 0;
}

.side-drawer nav ul li {
    margin: 20px 0;
}

.close-button {
    background: #f00;
    color: #fff;
    border: none;
    padding: 10px;
    cursor: pointer;
}
