.product-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  margin: 10px;
  text-align: center;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s;
  height: 60vh;
}

.product-card:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
}

.product-images-card {
  max-width: 100%;
  height: auto;
}

.product-title {
  font-size: 1.2em;
  margin: 10px 0;
  color: #333;
}

.product-description {
  font-size: 0.9em;
  color: #555;
}

.product-price {
  font-size: 1.1em;
  font-weight: bold;
  color: #b12704; /* Amazon red */
}

.add-to-cart-button,
.delete-button {
  background-color: #ffa41c; /* Amazon orange */
  color: white;
  border: none;
  padding: 10px;
  margin-top: 10px;
  cursor: pointer;
  border-radius: 3px;
  transition: background-color 0.3s;
}

.add-to-cart-button:hover,
.delete-button:hover {
  background-color: #ff8c00; /* Darker orange */
}

.delete-button {
  background-color: #d9534f; /* Bootstrap danger color */
}

.delete-button:hover {
  background-color: #c9302c; /* Darker red */
}
