.add-post-container {
  margin: 20px;
}
.animated-border {
  animation: borderAnimation 1s infinite alternate;
}

@keyframes borderAnimation {
  0% {
    border-color: #ff0000; /* Start color */
  }
  100% {
    border-color: #0000ff; /* End color */
  }
}

.add-post-form {
  display: flex;
  flex-direction: column;
}

.font-options, .title-font-options, .box-dimensions, .border-properties {
  margin-bottom: 20px;
}

label {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

button {
  padding: 10px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:disabled {
  background-color: #B0BEC5;
}

.preview {
  border: 1px solid #ccc;
  padding: 10px;
}

.preview-image {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
